<template>
  <v-container>
    <v-layout wrap>
      <v-flex xs12>
        <h1 class="titulo-principal">Histórico BAF</h1>
      </v-flex>

      <v-flex xs12>
        <v-form ref="formAgrupamento" lazy-validation>
          <v-flex v-show="listaHistoricoBaf.length > 0" xs12 md12 style="margin-top:27px;">
            <template>
              <v-card>
                <v-data-table
                  :headers="headers"
                  :items="listaHistoricoBaf"
                  item-key="historicoCustoPropostaId"
                  class="elevation-1"
                  locale="pt-BR"
                  :items-per-page="5"
                  :footer-props="{ itemsPerPageText: 'Linhas por página' }"
                />
              </v-card>
            </template>
          </v-flex>

          <v-flex x12 style="margin-top: 42px;" float-left>
            <v-btn id="btnVoltar" class="mr-4 botao-medio" color="primary" outlined @click="voltar">Sair</v-btn>
          </v-flex>
        </v-form>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions } from 'vuex'
import { historicoService } from '../services'
export default {
  data() {
    return {
      table: '',
      pagination: {
        sortBy: 'name'
      },
      listaHistoricoBaf: [],
      search: '',
      headers: [
        {
          text: 'Codigo',
          align: 'left',
          sortable: false,
          value: 'historicoCustoPropostaId'
        },
        { text: 'Moeda', value: 'moeda', width: 100, align: 'left' },
        { text: 'Validade De', value: 'dataValidadeIni', width: 120, align: 'left' },
        { text: 'Validade Ate', value: 'dataValidadeFim', width: 120, align: 'left' },
        { text: 'Tipo Custo ', value: 'descricaoTipoCusto', width: 150, align: 'left' },
        { text: 'Data Inclusão ', value: 'dataInclusao', width: 150, align: 'left' },
        { text: 'Usuario ', value: 'usuario', width: 120, align: 'left' },
        { text: '20 Dry Sul ', value: 'valCarga20DryS', width: 150, align: 'left' },
        { text: '20 Dry Norte ', value: 'valCarga20DryN', width: 150, align: 'left' },
        { text: '40 Dry Sul ', value: 'valCarga40DryS', width: 150, align: 'left' },
        { text: '40 Dry Norte ', value: 'valCarga40DryN', width: 150, align: 'left' },
        { text: '20 Reefer Sul ', value: 'valCarga20ReeferS', width: 150, align: 'left' },
        { text: '20 Reefer Norte ', value: 'valCarga20ReeferN', width: 150, align: 'left' },
        { text: '40 Reefer Sul ', value: 'valCarga40ReeferS', width: 150, align: 'left' },
        { text: '40 Reefer Norte ', value: 'valCarga40ReeferN', width: 150, align: 'left' }
      ],
      id: 0
    }
  },
  created() {
    const { id } = this.$route.query
    this.id = id
    this.buscarHistoricoBaf()
  },
  methods: {
    limpar() {
      this.$refs.formAgrupamento.resetValidation()
      this.form = {}
    },
    carregarAgrupamento(item) {
      this.form = item
    },
    async buscarHistoricoBaf() {
      try {
        var obj = {
          id: this.id
        }
        this.listaHistoricoBaf = await historicoService.getAll(obj)
      } catch (error) {
        this.setToast({
          color: 'warning',
          message: (error.response && error.response.data.Message) || error.message,
          time: 10000
        })
      }
    },
    voltar() {
      window.location = `../../CustoBafCad050.asp?CUSTO_PROPOSTA_ID=${this.id}`
    },
    ...mapActions(['setToast', 'setLoading', 'resetLoading'])
  }
}
</script>
<style>
