  width: 482px;
  height: 38px;
  text-align: left;
  font: Semibold 28px/38px Open Sans;
  letter-spacing: 0;
  color: #005799;
  opacity: 1;
}
.text-agrupamento {
  top: 184px;
  left: 294px;
  width: 334px;
  height: 48px;
  background: #ffffff 0% 0% no-repeat padding-box;
  border-radius: 5px;
  opacity: 1;
}
.label-agrupamento {
  top: 154px;
  left: 294px;
  height: 22px;
  text-align: left;
  font: Regular 16px/22px Open Sans;
  letter-spacing: 0;
  color: #475257;
  opacity: 1;
}
.botao-azul {
  background: #3498db 0% 0% no-repeat padding-box !important;
  color: #ffffff !important;
}
.botao-neutro {
  top: 878px;
  left: 412px;
  width: 100px;
  height: 40px;
  border: 1px solid #3498db;
  border-radius: 4px;
  opacity: 1;
}
.titulo-table {
  top: 284px;
  left: 316px;
  width: 234px;
  height: 27px;
  text-align: left;
  font: Semibold 20px/27px Open Sans;
  letter-spacing: 0;
  color: #475257;
  opacity: 1;
}

.botao-medio {
  height: 40px !important;
  text-align: center !important;
  font: Regular 14px/30px Open Sans !important;
  letter-spacing: 0 !important;
  width: 100px !important;
}
</style>
